.react-datepicker{ 
    width: 460px !important;
    border-radius: 0px !important;
    font-family: 'OpenSans', sans-serif !important;
    border: 2px solid #d9d9d9 !important;
}

.react-datepicker__day--selected { 
    background-color: #91a9ce !important;
    border-radius: 0px !important; 
    color: black !important;
}

.react-datepicker__day--today{ 
    border: 1px solid #dedede;
    font-weight: normal !important;
}
  
.react-datepicker__day--disabled{
    background-color:white !important;
}
  
.react-datepicker__navigation--next {
    right: 100px !important;
    border-left-color: black !important;
    margin-right: 10px !important;
}

.react-datepicker__navigation--previous {
    right: 10px;
    border-right-color: black !important;
    position: absolute !important;
    left: 300px !important;
}

.react-datepicker__header{
  border-radius: 0px !important;
}    

.react-datepicker__current-month{
    border-bottom: 1px solid #d7d7d7 !important; 
    padding-bottom: 3px !important;
    font-weight: normal !important;
    text-align: left !important;
    padding-left: 10px !important;
}    

.react-datepicker__header{
    background-color: white !important;
}

.react-datepicker__header--time{
    display: none !important;
}

.react-datepicker__day:hover{
    border-radius: 0px !important; background-color: #dbe6f8 !important;
}

.react-datepicker__time{
    padding-bottom: 10px !important;
    padding-top: 10px !important;
}

.react-datepicker__time-list{
    width: 98% !important;
}

.react-datepicker__time-list-item {
    height: 25px !important;    
    padding: 0px !important;
    padding-top: 6px !important;
    background-color: #e6e6e6 !important;
    margin-left: 5px !important;
    margin-right: 12px !important;
    border-radius: 3px !important;
    margin-top: 5px !important;
}

.react-datepicker__time-list-item:hover {
    border: 2px solid #7dadf1 !important;  
}

.react-datepicker__time-list-item--selected{
    background-color: #669999 !important;
    border-radius: 3px !important;
    /* color: white !important; */
}

.react-datepicker__time-list::-webkit-scrollbar{
width: 4px !important;
}

.react-datepicker__day-names{
    background-color: #f5f5f5 !important;
    width: 359px !important;
}

.react-datepicker__time-list::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px white; 
    box-shadow: inset 0 0 3px white; 
    border-radius: 30px !important;
    height: 100px !important;
    margin-left: 10px !important;
    -moz-box-shadow: inset 0 0 6px white; 
}
 
.react-datepicker__time-list::-webkit-scrollbar-thumb {
    border-radius: 10px !important;
    box-shadow: inset 0 0 3px black; height: 100px !important;
    -webkit-box-shadow: inset 0 0 6px black; height: 100px !important;
    -moz-box-shadow: inset 0 0 6px white; 
    margin-left: 10px !important;
}

.react-datepicker__time-container {
    width: 100px !important;
}

.react-datepicker__day{
    padding-right: 7px !important;
    padding-left: 7px !important;
}

.react-datepicker__day-name{
    padding-left: 7px !important;
    padding-right: 7px !important;
}

.react-datepicker__time-box{
    width: 97px !important;
}

.react-datepicker__day--outside-month{
    color : white !important;
}
.react-datepicker__day--outside-month:hover{
    color : white !important;
    background-color: white !important;
    cursor : default !important
}

@media screen and (max-width: 768px) {
    .react-datepicker{ 
        width: 200px !important;
        font-size: 9px !important;
    }
    .react-datepicker__time-container {
        width: 50px !important;
    }
    .react-datepicker__time-box{
        width: 57px !important;
    }
    .react-datepicker__day{
        padding-right: 0em !important;
        padding-left: 0em !important;
    }
    
    .react-datepicker__day-name{
        padding-left: 0.1em !important;
        padding-right: 0.1em !important;
    }
    .react-datepicker__day-names{
        width: 149px !important;
    }
    .react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name{
        width: 1.5em !important;
        line-height : 1em !important
    }
    .react-datepicker__header{
        text-align: left !important;
    }
    .react-datepicker__month{
        text-align: left !important;
        margin: 0px !important;
    }
    .react-datepicker__day{
        padding-left: 0.15em !important;
    }
    .react-datepicker__current-month{
        font-size: 10px !important;
    }
    .react-datepicker__navigation--next{
        right: 45px !important;
    }
    .react-datepicker__navigation--previous{
        left: 110px !important;
        margin-bottom: 5px !important;
    }
    .react-datepicker__navigation{
        top : 8px !important
    }
    .react-datepicker__time-list-item{
        height: 10px !important ;
        padding-top: 3px !important;
        padding-bottom: 2px !important;
        font-size: 7px !important;
    }
    .react-datepicker__time-list{
        width: 88% !important;
    }
    .react-datepicker__time-list-item {
        width: 75% !important;
    }


    .react-datepicker__time-list::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px white; 
    box-shadow: inset 0 0 6px white; 
    border-radius: 30px !important;
    height: 100px !important;
    margin-left: 8px !important;
    -moz-box-shadow: inset 0 0 6px white; 

}
 
.react-datepicker__time-list::-webkit-scrollbar-thumb {
    border-radius: 10px !important;
    box-shadow: inset 0 0 6px black;
    -webkit-box-shadow: inset 0 0 6px black;
     height: 50px !important;
    -moz-box-shadow: inset 0 0 6px white; 
    margin-left: 8px !important;

}
  }


