.form {
    background-color: #ffffff;
    height: 0;
    width: 60%;
    margin-right: auto;
    margin-top: 0px;
    padding: 0px;
    text-align:left;
}

.formNew {
    background-color: #FFF;
    width: 40%;
    margin-left: auto;
    margin-top: 0px;
    padding: 0px;
}

.headerStyleAppointment{
    margin-left: 45px;
    color: #232323;
    font-size: 14px;
    margin-top: 1px;
}

.headerStyleScheduled{
    margin-left: 45px;
    color: #232323;
    font-size: 14px;
    margin-top: 14px;
}

.label {
    color: #232323;
    font-size: 14px;
    height: 20px;
    width: 120px;
    margin-top: 10px;
    text-align: right;
    float:right;
}

.input {
    height: 20px;
    width: 450px;
    border: 1.5px solid #c9c9c9;
    margin-top: 10px;
    margin-left: 15px;
    padding: 5px;
    font-size: 14px;
    color: #232323;
}

.dropDown {
    height: 30px;
    width: 463px;
    border-bottom: none;
    border: 1.5px solid #c9c9c9;
    margin-top: 10px;
    margin-left: 14px;
    font-size: 14px; background-color: white; color: black;
}


.buttonConfirm { 
    border: none;
    background-color: #669999;
    color: #ffffff;
    margin-left: 10px;
    border-radius: 3px ;
    padding: 8px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;    
}

.buttonClear { 
    border: none;
    background-color: #e7e7dd;
    color: #232323;    
    border-radius: 3px;    
    padding: 8px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;    
}

.button {
    margin-top: 15px;
    text-align : right;
}

.tbltd{
    vertical-align: top;
    text-align: left;
}

.scheduledItem {
    color: #232323;
    padding-top: 2%;
    font-size: 14px;
    margin-left: -25px;
}

.iconDetails {
   margin-left:2%;
   float:left; 
} 
   
.container1 {
       width:100%;
       height:auto;
       padding:1%;
       margin-left: 10px;
       margin-top: -10px;
       margin-bottom: 15px;
}

.containe2 {
    width:100%;
    height:auto;
    padding:1%;
    margin-left: 35px;
    margin-top: -20px;
}

select {
    color:gray;
    padding-left: 5px !important;
}
   
select option:not(:first-child){
      color: black !important;
      padding-left: 20px !important;
}

option{
    padding-left: 20px !important;
}  

.mandatoryValidation{
    text-align: left;
    color: #ff8351;
    font-size: 15px;
    margin-left: 20px;
}

.WarningIcon{
    height: 16px;
    width: 16px;
    margin-top: 7px;
    margin-right: 8px;
}

.addAppDiv{
    width: 85%;
    border-right: 2px solid #d9d9d9;   
}

@media screen and (max-width: 768px) {
    .input{width: 192px;margin-left:11px;height: 15px;font-size: 13px;}
    .dropDown{width: 205px; margin-left: 11px; height: 25px;font-size: 12px !important;}
    .button{width: 217px; text-align: right;margin-top: 0px;font-size: 12px !important;}
    .buttonClear{
        padding: 5px 15px;
        font-size: 10px;
        text-align: center;
    }
    .buttonConfirm{
        padding: 5px 12px;
        font-size: 10px;
        text-align: center;
    }
    .mandatoryValidation{
        font-size: 10px ;
        margin-left: 20px;
    }
    .WarningIcon{
        width: 10px;
        height: 10px;
        margin-right: 5px;
    }
    .label{
        font-size: 12px !important;
    }
    
    .addAppDiv{
        width: 70%;
    }
  }


